import React, { useState, useEffect } from 'react';
import { Container, Card, Button, Badge, Alert, Row, Col, Spinner, ListGroup } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { getUserSubscriptions, deleteUserSubscription, isPaperInHistory, addPapersToHistory, getEmailSettings, getSubscriptions, refreshRecommendations, removeSubscription } from '../services/localData';
import { testKeywordMatch, sendEmailNotification } from '../services/api';
import './MySubscriptions.css';

function MySubscriptions() {
  const [subscriptions, setSubscriptions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [refreshingId, setRefreshingId] = useState(null);
  const [sendingEmailId, setSendingEmailId] = useState(null);
  const [activeSubscription, setActiveSubscription] = useState(null);
  const [matchedPapers, setMatchedPapers] = useState({});
  const [emailConfigured, setEmailConfigured] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    loadSubscriptions();
    
    // 检查邮箱配置
    const settings = getEmailSettings();
    setEmailConfigured(!!settings && !!settings.serviceId && !!settings.templateId && !!settings.emailjsId);
  }, []);

  const loadSubscriptions = () => {
    const subs = getUserSubscriptions();
    setSubscriptions(subs);
  };

  const handleDelete = (id) => {
    if (window.confirm('确定要删除这个订阅吗？')) {
      deleteUserSubscription(id);
      loadSubscriptions();
      setSuccess('订阅已删除');
    }
  };

  const handleRefresh = async (subscription) => {
    if (loading) return;
    setError(null);
    setSuccess(null);
    setRefreshingId(subscription.id);

    try {
      const result = await refreshRecommendations(subscription);
      if (result.success) {
        setSuccess(`已成功刷新『${subscription.keywords.join(', ')}』的推荐论文`);
        loadSubscriptions();
      } else {
        setError(`刷新失败: ${result.message || '未知错误'}`);
      }
    } catch (err) {
      setError(`刷新失败: ${err.message || '未知错误'}`);
    } finally {
      setRefreshingId(null);
    }
  };

  const handleSendEmail = async (subscription) => {
    try {
      // 检查是否配置了邮箱
      if (!emailConfigured) {
        setError('请先在"设置"页面配置您的EmailJS信息');
        return;
      }
      
      // 检查是否有推荐论文
      if (!subscription.recommendations || subscription.recommendations.length === 0) {
        setError(`${subscription.email} 没有匹配到论文，请先刷新推荐`);
        return;
      }

      setSendingEmailId(subscription.id);
      setError(null);
      setSuccess(null);

      console.log(`准备向 ${subscription.email} 发送 ${subscription.recommendations.length} 篇论文...`);
      
      const response = await sendEmailNotification(subscription.email, subscription.recommendations);
      if (response.success) {
        setSuccess(`已成功发送邮件至 ${subscription.email}`);
        // 将已发送的论文添加到历史记录
        addPapersToHistory(subscription.email, subscription.recommendations);
      } else {
        setError(`发送邮件失败: ${response.message || '未知错误'}`);
      }
    } catch (err) {
      console.error('发送邮件时出错:', err);
      // 提供更具体的错误信息以便调试
      if (err.message.includes('邮箱设置不完整')) {
        setError('EmailJS配置不完整。请确保您已在"设置"页面配置了 Service ID、Template ID 和 Public Key');
      } else if (err.message.includes('recipients')) {
        setError(`收件人地址错误: ${subscription.email}。请检查订阅邮箱地址是否有效`);
      } else {
        setError(`邮件发送错误: ${err.message || '未知错误'}`);
      }
    } finally {
      setSendingEmailId(null);
    }
  };

  return (
    <Container className="my-4">
      <h1>我的订阅</h1>
      
      {error && <Alert variant="danger">{error}</Alert>}
      {success && <Alert variant="success">{success}</Alert>}
      
      {!emailConfigured && (
        <Alert variant="warning">
          您尚未配置邮箱设置，无法发送邮件通知。
          <Button variant="link" onClick={() => navigate('/settings')}>
            前往设置
          </Button>
        </Alert>
      )}
      
      {subscriptions.length === 0 ? (
        <div className="text-center my-5">
          <p>您还没有订阅任何关键词。</p>
          <Link to="/subscribe" className="btn btn-primary">
            添加订阅
          </Link>
        </div>
      ) : (
        subscriptions.map(subscription => (
          <Card key={subscription.id} className="subscription-card mb-4">
            <Card.Header>
              <Row>
                <Col>
                  <h5 className="mb-0">{subscription.email}</h5>
                </Col>
                <Col xs="auto">
                  <Button 
                    variant="outline-danger" 
                    size="sm" 
                    onClick={() => handleDelete(subscription.id)}
                  >
                    删除
                  </Button>
                </Col>
              </Row>
            </Card.Header>
            <Card.Body>
              <Card.Title>关键词</Card.Title>
              <div className="keywords-container mb-3">
                {subscription.keywords.map((keyword, idx) => (
                  <Badge bg="primary" className="me-2 mb-2" key={idx}>
                    {keyword}
                  </Badge>
                ))}
              </div>
              <Card.Title>分类</Card.Title>
              <div className="categories-container mb-3">
                {subscription.categories.map((category, idx) => (
                  <Badge bg="secondary" className="me-2 mb-2" key={idx}>
                    {category}
                  </Badge>
                ))}
              </div>
              <div className="d-flex justify-content-between mt-3">
                <div>
                  <Button 
                    variant="outline-primary" 
                    size="sm"
                    onClick={() => handleRefresh(subscription)}
                    disabled={loading && refreshingId === subscription.id}
                  >
                    {loading && refreshingId === subscription.id ? (
                      <>
                        <Spinner as="span" animation="border" size="sm" role="status" aria-hidden="true" />
                        <span className="ms-2">刷新中...</span>
                      </>
                    ) : '刷新推荐'}
                  </Button>
                  
                  <Button 
                    variant="outline-info" 
                    size="sm" 
                    onClick={() => handleSendEmail(subscription)}
                    disabled={sendingEmailId === subscription.id || !emailConfigured}
                  >
                    {sendingEmailId === subscription.id ? (
                      <>
                        <Spinner animation="border" size="sm" /> 发送中...
                      </>
                    ) : '发送邮件'}
                  </Button>
                </div>
                <small className="text-muted">
                  上次更新: {new Date(subscription.lastUpdated).toLocaleString()}
                </small>
              </div>
            </Card.Body>
            {matchedPapers[subscription.id] && (
              <Card.Footer>
                <Card.Title>匹配论文</Card.Title>
                {Object.keys(matchedPapers[subscription.id]).length === 0 ? (
                  <Alert variant="info">
                    未找到新的匹配论文
                  </Alert>
                ) : (
                  Object.entries(matchedPapers[subscription.id]).map(([category, papers]) => (
                    <div key={category} className="mb-3">
                      <h5>{category}</h5>
                      <ListGroup variant="flush">
                        {papers.slice(0, 5).map((paper, idx) => (
                          <ListGroup.Item key={idx}>
                            <div className="d-flex justify-content-between align-items-start">
                              <div>
                                <a href={paper.link} target="_blank" rel="noreferrer">
                                  {paper.title}
                                </a>
                                <div className="text-muted small">
                                  {paper.authors.slice(0, 3).join(', ')}
                                  {paper.authors.length > 3 && ' 等'}
                                </div>
                                <div>
                                  <small>
                                    匹配关键词: {paper.matched_keywords.join(', ')}
                                  </small>
                                </div>
                              </div>
                              <Badge bg="info">
                                {new Date(paper.published).toLocaleDateString()}
                              </Badge>
                            </div>
                          </ListGroup.Item>
                        ))}
                        {papers.length > 5 && (
                          <ListGroup.Item className="text-center">
                            <small>还有 {papers.length - 5} 篇相关论文</small>
                          </ListGroup.Item>
                        )}
                      </ListGroup>
                    </div>
                  ))
                )}
              </Card.Footer>
            )}
          </Card>
        ))
      )}
    </Container>
  );
}

export default MySubscriptions; 